"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import { Plus, CheckCircle2, Circle, Edit2, Trash2, Save, X, AlertCircle, Loader2 } from "lucide-react";
import { api } from "@/lib/api-client";
import authClient from "@/lib/auth";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";

interface Task {
  id: number;
  title: string;
  description: string | null;
  is_completed: boolean;
  created_at: string;
  updated_at: string;
  user_id: number;
}

export default function TasksPage() {
  const router = useRouter();
  const [tasks, setTasks] = useState<Task[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState("");

  const [newTaskTitle, setNewTaskTitle] = useState("");
  const [newTaskDescription, setNewTaskDescription] = useState("");
  const [isCreating, setIsCreating] = useState(false);

  const [editingTaskId, setEditingTaskId] = useState<number | null>(null);
  const [editTitle, setEditTitle] = useState("");
  const [editDescription, setEditDescription] = useState("");

  useEffect(() => {
    fetchTasks();
  }, []);

  const fetchTasks = async () => {
    try {
      setIsLoading(true);
      setError("");
      const response = await api.get<{ tasks: Task[]; total: number }>("/tasks");
      setTasks(response.tasks);
    } catch (err) {
      if (err instanceof Error) {
        if (err.message.includes("Authentication")) {
          return;
        }
        setError(err.message);
      } else {
        setError("Failed to load tasks");
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleCreateTask = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!newTaskTitle.trim()) {
      setError("Task title is required");
      return;
    }

    try {
      setIsCreating(true);
      setError("");

      await api.post<Task>("/tasks", {
        title: newTaskTitle,
        description: newTaskDescription || null,
      });

      setNewTaskTitle("");
      setNewTaskDescription("");
      await fetchTasks();
    } catch (err) {
      if (err instanceof Error) {
        if (err.message.includes("Authentication")) {
          return;
        }
        setError(err.message);
      } else {
        setError("Failed to create task");
      }
    } finally {
      setIsCreating(false);
    }
  };

  const handleToggleComplete = async (task: Task) => {
    try {
      setError("");

      setTasks(prevTasks =>
        prevTasks.map(t =>
          t.id === task.id ? { ...t, is_completed: !t.is_completed } : t
        )
      );

      await api.put<Task>(`/tasks/${task.id}`, {
        is_completed: !task.is_completed,
      });
    } catch (err) {
      setTasks(prevTasks =>
        prevTasks.map(t =>
          t.id === task.id ? { ...t, is_completed: task.is_completed } : t
        )
      );

      if (err instanceof Error) {
        if (err.message.includes("Authentication")) {
          return;
        }
        setError(err.message);
      } else {
        setError("Failed to update task");
      }
    }
  };

  const handleStartEdit = (task: Task) => {
    setEditingTaskId(task.id);
    setEditTitle(task.title);
    setEditDescription(task.description || "");
  };

  const handleCancelEdit = () => {
    setEditingTaskId(null);
    setEditTitle("");
    setEditDescription("");
  };

  const handleSaveEdit = async (taskId: number) => {
    if (!editTitle.trim()) {
      setError("Task title is required");
      return;
    }

    try {
      setError("");
      await api.put<Task>(`/tasks/${taskId}`, {
        title: editTitle,
        description: editDescription || null,
      });
      setEditingTaskId(null);
      await fetchTasks();
    } catch (err) {
      if (err instanceof Error) {
        if (err.message.includes("Authentication")) {
          return;
        }
        setError(err.message);
      } else {
        setError("Failed to update task");
      }
    }
  };

  const handleDeleteTask = async (taskId: number) => {
    if (!confirm("Are you sure you want to delete this task?")) {
      return;
    }

    try {
      setError("");
      await api.delete(`/tasks/${taskId}`);
      await fetchTasks();
    } catch (err) {
      if (err instanceof Error) {
        if (err.message.includes("Authentication")) {
          return;
        }
        setError(err.message);
      } else {
        setError("Failed to delete task");
      }
    }
  };

  const completedTasks = tasks.filter(t => t.is_completed).length;
  const totalTasks = tasks.length;

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary/5 via-background to-muted/20 py-8 px-4 sm:px-6 lg:px-8">
      <div className="container mx-auto max-w-7xl">
        {/* Header with Stats */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="mb-8"
        >
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-6">
            <div>
              <h1 className="text-3xl md:text-4xl lg:text-5xl font-bold tracking-tight mb-2">My Tasks</h1>
              <p className="text-sm md:text-base text-muted-foreground">
                Manage your tasks and track your progress
              </p>
            </div>
            <div className="flex gap-3">
              <Badge variant="secondary" className="text-sm md:text-base px-4 py-2 shadow-sm">
                {completedTasks} / {totalTasks} completed
              </Badge>
            </div>
          </div>
        </motion.div>

        {/* Error Alert */}
        <AnimatePresence>
          {error && (
            <motion.div
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -10 }}
              className="mb-6"
            >
              <div className="flex items-start gap-2 rounded-lg bg-destructive/10 p-4 text-sm text-destructive border border-destructive/20 shadow-sm">
                <AlertCircle className="h-5 w-5 mt-0.5 flex-shrink-0" />
                <span>{error}</span>
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        <div className="grid gap-6 lg:grid-cols-3">
          {/* Create Task Form */}
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.1 }}
            className="lg:col-span-1"
          >
            <Card className="sticky top-20 shadow-xl border-2">
              <CardHeader className="pb-4">
                <CardTitle className="flex items-center gap-2 text-xl md:text-2xl">
                  <Plus className="h-6 w-6" />
                  New Task
                </CardTitle>
                <CardDescription className="text-sm md:text-base">Create a new task to get started</CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleCreateTask} className="space-y-5">
                  <div className="space-y-2">
                    <Label htmlFor="new-title" className="text-sm font-medium">Title *</Label>
                    <Input
                      id="new-title"
                      placeholder="Enter task title"
                      value={newTaskTitle}
                      onChange={(e) => setNewTaskTitle(e.target.value)}
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="new-description" className="text-sm font-medium">Description</Label>
                    <Textarea
                      id="new-description"
                      placeholder="Add details (optional)"
                      value={newTaskDescription}
                      onChange={(e) => setNewTaskDescription(e.target.value)}
                      rows={3}
                    />
                  </div>
                  <Button type="submit" className="w-full shadow-md" disabled={isCreating}>
                    {isCreating ? (
                      <>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        Creating...
                      </>
                    ) : (
                      <>
                        <Plus className="mr-2 h-4 w-4" />
                        Create Task
                      </>
                    )}
                  </Button>
                </form>
              </CardContent>
            </Card>
          </motion.div>

          {/* Tasks List */}
          <motion.div
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="lg:col-span-2"
          >
            <Card className="shadow-xl border-2">
              <CardHeader className="pb-4">
                <CardTitle className="text-xl md:text-2xl">Your Tasks</CardTitle>
                <CardDescription className="text-sm md:text-base">
                  {totalTasks === 0
                    ? "No tasks yet"
                    : `${totalTasks} task${totalTasks !== 1 ? "s" : ""} total`}
                </CardDescription>
              </CardHeader>
              <CardContent>
                {isLoading ? (
                  <div className="flex flex-col items-center justify-center py-16 text-muted-foreground">
                    <Loader2 className="h-10 w-10 animate-spin mb-4 text-primary" />
                    <p className="text-sm md:text-base">Loading tasks...</p>
                  </div>
                ) : tasks.length === 0 ? (
                  <div className="flex flex-col items-center justify-center py-16 text-center">
                    <div className="rounded-full bg-primary/10 p-4 mb-4 shadow-sm">
                      <CheckCircle2 className="h-10 w-10 text-primary" />
                    </div>
                    <h3 className="font-semibold text-lg md:text-xl mb-2">No tasks yet</h3>
                    <p className="text-sm md:text-base text-muted-foreground max-w-sm">
                      Create your first task using the form on the left to get started!
                    </p>
                  </div>
                ) : (
                  <div className="space-y-3">
                    <AnimatePresence mode="popLayout">
                      {tasks.map((task, index) => (
                        <motion.div
                          key={task.id}
                          initial={{ opacity: 0, y: 20 }}
                          animate={{ opacity: 1, y: 0 }}
                          exit={{ opacity: 0, x: -100 }}
                          transition={{ duration: 0.3, delay: index * 0.05 }}
                          layout
                        >
                          {editingTaskId === task.id ? (
                            <Card className="border-primary border-2 shadow-lg">
                              <CardContent className="pt-6">
                                <div className="space-y-4">
                                  <Input
                                    value={editTitle}
                                    onChange={(e) => setEditTitle(e.target.value)}
                                    placeholder="Task title"
                                    className="text-base"
                                  />
                                  <Textarea
                                    value={editDescription}
                                    onChange={(e) => setEditDescription(e.target.value)}
                                    placeholder="Task description"
                                    rows={2}
                                  />
                                  <div className="flex gap-2">
                                    <Button
                                      size="sm"
                                      onClick={() => handleSaveEdit(task.id)}
                                      className="shadow-md"
                                    >
                                      <Save className="mr-2 h-4 w-4" />
                                      Save
                                    </Button>
                                    <Button
                                      size="sm"
                                      variant="outline"
                                      onClick={handleCancelEdit}
                                    >
                                      <X className="mr-2 h-4 w-4" />
                                      Cancel
                                    </Button>
                                  </div>
                                </div>
                              </CardContent>
                            </Card>
                          ) : (
                            <Card
                              className={cn(
                                "transition-all duration-300 hover:shadow-xl cursor-pointer border-2",
                                task.is_completed && "bg-muted/30"
                              )}
                            >
                              <CardContent className="pt-6">
                                <div className="flex items-start gap-3">
                                  <button
                                    onClick={() => handleToggleComplete(task)}
                                    className="mt-0.5 transition-all duration-200 hover:scale-110 active:scale-95"
                                  >
                                    {task.is_completed ? (
                                      <CheckCircle2 className="h-6 w-6 text-primary" />
                                    ) : (
                                      <Circle className="h-6 w-6 text-muted-foreground hover:text-primary" />
                                    )}
                                  </button>
                                  <div className="flex-1 min-w-0">
                                    <h3
                                      className={cn(
                                        "font-semibold text-base md:text-lg mb-1",
                                        task.is_completed &&
                                          "line-through text-muted-foreground"
                                      )}
                                    >
                                      {task.title}
                                    </h3>
                                    {task.description && (
                                      <p
                                        className={cn(
                                          "text-sm md:text-base text-muted-foreground leading-relaxed",
                                          task.is_completed && "line-through"
                                        )}
                                      >
                                        {task.description}
                                      </p>
                                    )}
                                    <div className="flex items-center gap-2 mt-3">
                                      <Badge
                                        variant={
                                          task.is_completed ? "success" : "secondary"
                                        }
                                        className="text-xs"
                                      >
                                        {task.is_completed ? "Completed" : "Pending"}
                                      </Badge>
                                      <span className="text-xs text-muted-foreground">
                                        {new Date(task.created_at).toLocaleDateString()}
                                      </span>
                                    </div>
                                  </div>
                                  <div className="flex gap-1">
                                    <Button
                                      size="icon"
                                      variant="ghost"
                                      onClick={() => handleStartEdit(task)}
                                      className="h-9 w-9 hover:bg-primary/10"
                                    >
                                      <Edit2 className="h-4 w-4" />
                                    </Button>
                                    <Button
                                      size="icon"
                                      variant="ghost"
                                      onClick={() => handleDeleteTask(task.id)}
                                      className="h-9 w-9 text-destructive hover:text-destructive hover:bg-destructive/10"
                                    >
                                      <Trash2 className="h-4 w-4" />
                                    </Button>
                                  </div>
                                </div>
                              </CardContent>
                            </Card>
                          )}
                        </motion.div>
                      ))}
                    </AnimatePresence>
                  </div>
                )}
              </CardContent>
            </Card>
          </motion.div>
        </div>
      </div>
    </div>
  );
}
